        />
      </svg>
    </template>
    <template #value>
      {{ $t(selectedLanguage.labelKey) }}
    </template>
    <SingleSelect
      :value="selectedLanguage"
      @change="setLanguage"
      name="country"
      :options="supportedLocales"
      :getValue="(language, index) => getOr(language.code, `val-${index}`)"
      :getLabel="(language) => `${getOr($t(language.labelKey), 'n/a')} (${getOr(language.code, 'n/a')})`"
      :applyOnChange="true"
    />
  </ConfigDropdown>
</template>
